import HomeHeader from '../components/HomeHeader';
import Footer from '../components/Footer';

export default function Contact() {
  return (
  <div>
    <HomeHeader />
    <form className="contact-form" onSubmit={ (e) => this.formSubmit(e) }>
      <label class="message" htmlFor="message-input">Your Message</label>
      <textarea  name="message" class="message-input" type="text" placeholder="Please write your message here" value={this.state.message} required/>

      <label class="message-name" htmlFor="message-name">Your Name</label>
      <input me="name" class="message-name" type="text" placeholder="Your Name" value={this.state.name}/>

      <label class="message-email" htmlFor="message-email">Your Email</label>
      <input  name="email" class="message-email" type="email" placeholder="your@email.com" required value={this.state.email} />

      <div className="button--container">
          <button type="submit" className="button button-primary">{ this.state.buttonText }</button>
      </div>
    </form>
    <Footer />
  </div>
  );
};